/* eslint-disable no-console */
// 
if (window) {
  window.onerror = function(message, source, line, column, error) {
    console.log(
      `window.onerror(message=${message}, source=${source}, line=${line}, column=${column})`,
      error
    )
  }
  window.onunhandledrejection = function(evt) {
    if (evt.promise) {
      evt.promise.then(undefined, err => {
        if (err instanceof TmsIgnorableError) {
          console.log('')
        } else {
          console.error('err rejection', err)
        }
      })
    }
    evt.preventDefault()
  }
}
// 
class TmsIgnorableError extends Error {}
/**
 *
 */
class VueErrorHandler {
  /**
   *
   * @param {object} options
   * @param {function} options.handler 
   */
  constructor(options) {
    if (typeof options === 'object') {
      if (typeof options.handler === 'function') {
        this.outerHandler = options.handler
      }
      if (typeof options.before === 'function') {
        this.beforeHandler = options.before
      }
      if (typeof options.after === 'function') {
        this.afterHandler = options.after
      }
    }
  }
  /**
   * Vue
   *
   * @param {*} error
   * @param {*} vm
   * @param {*} info Vue 
   */
  async handle(error, vm, info) {
    if (!error || error instanceof TmsIgnorableError) return
    try {
      if (this.beforeHandler) await this.beforeHandler(error, vm, info)

      if (this.outerHandler) {
        await this.outerHandler(error, vm, info)
      } else {
        console.error(`: ${error.message}\nVue: ${info}`)
      }

      if (this.afterHandler) await this.afterHandler(error, vm, info)
    } catch (e) {
      console.error('', e)
    }
  }
}

export { TmsIgnorableError }

export default function install(Vue, options) {
  const handler = new VueErrorHandler(options)
  Vue.config.errorHandler = function(...args) {
    handler.handle(...args)
  }
}
